import { _decorator, Component, Node, Prefab, randomRangeInt, instantiate, Vec3, CCBoolean, CCInteger, find, CCFloat, Game, ParticleSystem } from 'cc';
import { Coche } from './Coche';
import { SelladoPieza } from './SelladoPieza';
const { ccclass, property } = _decorator;

@ccclass('SpawnPiso')
export class SpawnPiso extends Component {
    @property([Prefab])
    public prefab: Prefab[] = []; // Prefab para representar los Pisos del mapa
    private creando: boolean = false;
    private distanciax : number = 0;
    @property(Node)
    public GameObjetc:Node[] = [];
    private distanciaz : number = 0;
    @property(CCBoolean)
    public derecha : boolean = false;
    @property(CCBoolean)
    public izquierda : boolean = false;
    @property(CCInteger)
    public valorMax : number = 3;
    private PowerUp : boolean = false;
    @property(CCInteger)
    private LimiteH : number = 2;
    @property(CCInteger)
    private LimiteV : number = 2;
    @property(CCInteger)
    private horizontal : number = 0;
    @property(CCInteger)
    private vertical : number = 0;
    @property(CCInteger)
    private rotaty : number = 0 ;
    @property(CCFloat)
    public tiempo : number = 0;
    @property(CCBoolean)
    public Muerto : boolean = false;
    @property(CCInteger)
    public random : number = 0;
    @property(CCInteger)
    private index : number = 0;
    private indexPiezasOrdenar = 0;
    private coche : Node = null;
    
    
    protected onLoad(): void {
        this.vertical = 3;
        this.coche = find("COCHEa");
        //this.tiempo = node.getComponent(Coche).tiempoSpawn;
        
    }
    start() {

    }
    Power( i : number){
        this.PowerUp = true;
        //this.valorMax = 1;
        this.indexPiezasOrdenar = i
    }
    asignaindex( pieza: Node, i : number){
        pieza.getComponent(SelladoPieza).IndexPieza = i;
    }
    crea() {
       // const node = find("coche");
        //this.tiempo = node.getComponent(Coche).tiempoSpawn;
        if(this.izquierda || this.derecha){

            if(this.derecha ){
                this.distanciaz -=8;
                const camino : number = randomRangeInt(1,3);
                switch(camino){
                    case 1:
                        //seguir el camino en horizontal
                        this.horizontal+=1;
                        if(this.horizontal > this.LimiteH){
                            const node2 = instantiate(this.prefab[4]);
                           node2.parent = this.node;
                           node2.setPosition(new Vec3(this.distanciax, 0, this.distanciaz));
                           this.RotaRandomPieza(node2);
                           
                            this.creando = false;
                           this.derecha=false;
                           this.GameObjetc.push(node2);
                          
                           break;
                        }
                        
                        const node1 = instantiate(this.prefab[3]);
                           node1.parent = this.node;
                           node1.setPosition(new Vec3(this.distanciax, 0, this.distanciaz));
                           this.RotaRandomPieza(node1);
                          
                            this.creando = false;
                           this.GameObjetc.push(node1);
                        break;
                    case 2:
                        //vuelta izq
                        
                        const node2 = instantiate(this.prefab[4]);
                           node2.parent = this.node;
                           node2.setPosition(new Vec3(this.distanciax, 0, this.distanciaz));
                           this.RotaRandomPieza(node2);
                           
                            this.creando = false;
                           this.derecha=false;
                           this.GameObjetc.push(node2);
                           
                           
                        break;
                       
                }
            }
            else{
                this,this.distanciaz +=8;
                const camino : number = randomRangeInt(1,3);
                switch(camino){
                    case 1:
                        //seguir el camino horizontal a la izquierda
                        this.horizontal+=1;
                        if(this.horizontal > this.LimiteH){
                            const node2 = instantiate(this.prefab[5]);
                           node2.parent = this.node;
                           node2.setPosition(new Vec3(this.distanciax, 0, this.distanciaz));
                           this.RotaRandomPieza(node2);
                            
                            this.creando = false;
                           this.izquierda=false;
                           this.GameObjetc.push(node2)
                           break;
                        }
                        const node1 = instantiate(this.prefab[3]);
                           node1.parent = this.node;
                           node1.setPosition(new Vec3(this.distanciax, 0, this.distanciaz));
                           this.RotaRandomPieza(node1);
                           node1.getComponent(SelladoPieza).Derecha = true;
                            this.creando = false;
                           this.GameObjetc.push(node1);
                        break;
                    case 2:
                        //vuelta izq
                        const node2 = instantiate(this.prefab[5]);
                           node2.parent = this.node;
                           node2.setPosition(new Vec3(this.distanciax, 0, this.distanciaz));
                           this.RotaRandomPieza(node2);
                            
                            this.creando = false;
                           this.izquierda=false;
                           this.GameObjetc.push(node2);
                           
                        break;
                       
                }
            }
        }
        
        
        else{
            this.horizontal =0;
            
            this.distanciax -=8;
            const Inst:number = randomRangeInt(1,this.valorMax);         
            switch(Inst){
                case 1:
                   //vertcial
                   this.vertical+=1;
                   if(this.vertical >= this.LimiteV){
                        let opcion = randomRangeInt(1,3)
                        switch(opcion){
                            case 1:
                            const node2 = instantiate(this.prefab[1]);
                            node2.parent = this.node;
                            node2.setPosition(new Vec3(this.distanciax, 0, this.distanciaz));
                            const randomd : number = randomRangeInt(2,4);
                       
                            this.RotaRandomPieza(node2);
                            
                            this.creando = false;
                            this.derecha = true;
                            this.GameObjetc.push(node2);
                            this.vertical=0;
                            break;
                    case 2:
                        const node3 = instantiate(this.prefab[2]);
                       node3.parent = this.node;
                       node3.setPosition(new Vec3(this.distanciax, 0, this.distanciaz));
                      this.RotaRandomPieza(node3);
                       
                       this.creando = false;
                       this.izquierda=true;
                       this.GameObjetc.push(node3);
                       this.vertical=0;
                       break;

                    }

                   }
                   else{
                    const node1 = instantiate(this.prefab[0]);
                    node1.parent = this.node;
                    node1.setPosition(new Vec3(this.distanciax, 0, this.distanciaz));
                    this.RotaRandomPieza(node1);
                    this.creando = false;
                    this.GameObjetc.push(node1);
                   }
                       
                   
                    break;
                    case 2:
                       //Derecha
                       this.vertical=0;
                       const node2 = instantiate(this.prefab[1]);
                       node2.parent = this.node;
                       node2.setPosition(new Vec3(this.distanciax, 0, this.distanciaz));
                      this.RotaRandomPieza(node2);
                       
                       this.creando = false;
                       this.derecha = true;
                       this.GameObjetc.push(node2);
                       break;
                   case 3:
                       //IZQ
                       this.vertical=0;
                       const node3 = instantiate(this.prefab[2]);
                       node3.parent = this.node;
                       node3.setPosition(new Vec3(this.distanciax, 0, this.distanciaz));
                      
                       this.RotaRandomPieza(node3);
                       this.creando = false;
                       this.izquierda=true;
                       this.GameObjetc.push(node3);
                       break;
            }
        }
        
        
    }
    ordenaPiezas(ind: number){
        console.log("Ordena desde la pieza " + ind.toString());
        let max : number = ind +4;
        this.coche.getComponent(Coche).Score += 4;
        this.coche.getComponent(Coche).ScoreText.string= this.coche.getComponent(Coche).Score.toString();
        const inicio : number = ind+1;
        if(max> this.GameObjetc.length){
            max = this.GameObjetc.length;
        }
        for(let i = inicio; i < max; i++ ){
            console.log("Estoy entrando aqui??" + i.toString());
            this.GameObjetc[i].setRotationFromEuler(new Vec3(0,0,0));
            this.GameObjetc[i].getChildByName("Particulas").getComponent(ParticleSystem).play();
            this.GameObjetc[i].getComponent(SelladoPieza).Sellado=true;
            this.GameObjetc[i].getComponent(SelladoPieza).yaPaso = true;
        }
        
    }
    RotaRandomPieza(Pieza : Node){
        
        this.asignaindex(Pieza, this.index);
        this.index +=1;
        this.random= randomRangeInt(2,4);
       // this.random = 1;
        switch(this.random){
            case 1:
                this.rotaty = 0;
            break;
            case 2: 
                this.rotaty = 90;
            break;
            case 3:
                this.rotaty=180;
            break;

        }
        Pieza.setRotationFromEuler(new Vec3(0,this.rotaty,0))
    }
    update(deltaTime: number) {
        if (!this.creando && !this.Muerto ) {
            this.creando = true;
            this.scheduleOnce(function () {
                this.crea();
 
            }, this.tiempo);
        }
        if(this.PowerUp){
            this.PowerUp = false;
            this.ordenaPiezas(this.indexPiezasOrdenar);
        }
        
    }
}


