import { _decorator, Component, Node, Collider, ICollisionEvent } from 'cc';
import{Coche} from "./Coche";
const { ccclass, property } = _decorator;

@ccclass('CollisionesPared')
export class CollisionesPared extends Component {



    protected onLoad(): void {
        const collider = this.getComponent(Collider);
        if(collider){
           
            collider.on('onTriggerEnter',this.onControllerTriggerEnter, this)
            collider.on('onCollisionExit', this.onCollisionExit, this);
        }
        
        
    }
    onControllerTriggerEnter(event: ICollisionEvent){
        //console.log(event.otherCollider.getGroup().toString());
        if(event.otherCollider.getGroup()== 2){
            //console.log("Chocaste contra pared nub");
            this.node.parent.getComponent(Coche).AnularFuerzas();
        }
       

    }
   
    onCollisionExit(event: ICollisionEvent){
        
    }
    start() {

    }

    update(deltaTime: number) {
        
    }
}


