import { _decorator, CCBoolean, CCFloat, Component, find, Label, Node, SpriteFrame, sys, Sprite, MeshRenderer, Mesh, Color, Material } from 'cc';
const { ccclass, property } = _decorator;
import { ManagerGeneral } from './ManagerGeneral';

@ccclass('Manager')
export class Manager extends Component 
{
    @property(Node) player:Node=null;
    @property([Mesh]) modelos:Mesh[]=[];

    

    @property([Material]) materiales:Material[]=[];
    
    

    @property(CCFloat) modeloCoche:number=0;
    @property(CCFloat) miColor:number=0;

    @property(ManagerGeneral)managerG:ManagerGeneral=null;

    a:Node=null;

    
    /*
    @property (Color) cAzul = new Color(110, 135, 231, 255);
    @property (Color) cAmarillo = new Color(226, 231, 110, 255);
    @property (Color) cVerde = new Color(110, 231, 110, 255);
    @property (Color) cRojo = new Color(231, 110, 110, 255);
    @property (Color) cNaranja = new Color(231, 187, 110, 255);*/

    
    

    protected onLoad(): void 
    {
        //this.player=find("Player");
        
        this.a=find("Canvas/ManagerGeneral");
        this.managerG=this.a.getComponent(ManagerGeneral);
        //this.managerG.LoadData();
        
        this.managerG.CargaCoche();

        this.modeloCoche=this.managerG.modeloID;
        this.miColor=this.managerG.colorID;
        console.log(this.miColor);
        

        this.MiCoche();
        
        
    }

    start() {

    }

    update(deltaTime: number) 
    {
        
    }

    MiCoche()
    {
        console.log("this.miColor");
        switch(this.modeloCoche)
        {
            case 0:
                this.player.getComponent(MeshRenderer).mesh=this.modelos[0];
                this.player.getComponent(MeshRenderer).setSharedMaterial(this.materiales[this.miColor],0);
                //this.ColorCoche(this.player.getComponent(MeshRenderer));
                
                break;
            case 1:
                this.player.getComponent(MeshRenderer).mesh=this.modelos[1];
                this.player.getComponent(MeshRenderer).setSharedMaterial(this.materiales[this.miColor],0);
               
                break;
            case 2:
                this.player.getComponent(MeshRenderer).mesh=this.modelos[2];
                this.player.getComponent(MeshRenderer).setSharedMaterial(this.materiales[this.miColor],0);
               
                break;
        }
        
    }

    
}


