import { _decorator, CCBoolean, Component, find, Node, Vec3, math, Camera } from 'cc';
import { SpawnPiso } from './SpawnPiso';
const { ccclass, property } = _decorator;

@ccclass('FollowPlayer')
export class FollowPlayer extends Component {

    @property(Node)
    public player : Node;

    @property(Node)
    coche : Node;
    @property(Node)
    public SpawnP : Node; 
    @property(CCBoolean)
    public derecha = false;
    @property(CCBoolean)
    private aleja : boolean = false;
    

    onLoad() {
        this.scheduleOnce(function(){
            this.aleja = true;
        },3.5)
        this.SpawnP = find("ManagerMapa");
       /* for(let i = 0 ; i< this.player.children.length; i++)
            {
                 if(this.player.children[i].active)
                    {
                        this.coche = this.player.children[i];
                    }
            }*/
        
    }
    start() {

    }
    mueveCamara(deltaTime: number, target : Vec3){
        let currentPosition : Vec3 = this.node.position.clone();
        Vec3.lerp(currentPosition, this.node.position, target , .5 * deltaTime ); 
        this.node.setPosition( currentPosition);
    }
     private zoomOut(deltaTime : number){
        this.node.getComponent(Camera).orthoHeight = math.lerp(this.node.getComponent(Camera).orthoHeight, 25, 0.5 * deltaTime);
    }
    update(deltaTime: number) {
        if(this.derecha){
           const targetD = new Vec3(this.coche.position.x-23,this.coche.position.y +20, this.coche.position.z+27)
           this.mueveCamara(deltaTime,targetD)
        }
        else{
            const targetI = new Vec3(this.coche.position.x-25,this.coche.position.y +20, this.coche.position.z+20)
            this.mueveCamara(deltaTime,targetI);
        }
        if(this.aleja){
            this.zoomOut(deltaTime);
        }
    }
}


