import { _decorator, Component, Node, find, CCBoolean, CCFloat, Button, Label, SpriteFrame, Sprite, CCString, CCInteger } from 'cc';
import { ManagerGeneral } from './ManagerGeneral';
import { SeleccionColor } from './SeleccionColor';
const { ccclass, property } = _decorator;

@ccclass('CompraObjetos')
export class CompraObjetos extends Component 
{
    managerG:ManagerGeneral;
    @property (Node) ma:Node=null;
    //@property(CCBoolean) moneda:Boolean;
    @property(CCFloat) costo:number;
    @property(Label) miCosto:Label=null;

    @property(Node) colores:Node=null;
    @property(SpriteFrame) azul:SpriteFrame=null;
    @property(SpriteFrame) modelo:SpriteFrame=null;
    @property(CCFloat) desMOD:number=0;

    @property(CCString) nombre:string="";
    @property(CCFloat) modeloID:number=0;
    



    protected onLoad(): void 
    {
        this.ma=find("Canvas/ManagerGeneral");
        this.managerG=this.ma.getComponent(ManagerGeneral);
        //this,this.managerG.LoadData();
        this.desMOD=this.managerG.CargaCocheDesbloqueado(this.nombre,this.desMOD);

        this.miCosto.string=this.costo.toString();
        this.MeAlcanza();
        
    }

    start() 
    {
        
        if(this.desMOD==1)
            {
                console.log("modelo desbloqueado");
                this.node.parent.children[1].getComponent(Sprite).spriteFrame=this.modelo;
                this.colores.active=true;
                
                this.ma.getComponent(SeleccionColor).Colores("azul");
                this.colores.children[0].getComponent(Sprite).spriteFrame=this.azul;
                this.colores.children[0].getComponent(Button).normalSprite=this.azul;
                //this.ma.getComponent(SeleccionColor).MeSeleccionaron(this.modeloID);
                this.node.active=false;
        
            }
            
        
    }

    update(deltaTime: number) {
        
    }

    MeAlcanza()
    {
        if(this.costo<=this.managerG.contadorTarjetas)
        {
            this.node.getComponent(Button).interactable=true;
        }
        else
        {
            this.node.getComponent(Button).interactable=false;
            this.node.getComponent(Button).onDisable();
        }
    }

    Compra()
    {
        this.managerG.ActualizaCompras(this.costo,"tarjeta");
                /*this.managerG.control=this.control;
                this.managerG.iman=this.iman;*/

                this.ma.getComponent(SeleccionColor).Colores("azul");
                
                this.colores.active=true;
                this.colores.children[0].getComponent(Sprite).spriteFrame=this.azul;
                this.colores.children[0].getComponent(Button).normalSprite=this.azul;

                this.managerG.colorID=0;
                this.node.parent.children[1].getComponent(Sprite).spriteFrame=this.modelo;
                this.managerG.modeloID=this.modeloID;
                this.managerG.CocheDesbloqueado(1,this.nombre);
        this.managerG.Save();
        

        this.node.active=false;

    }
}


