import { _decorator, Component, Node, find } from 'cc';
import { SeleccionColor } from './SeleccionColor';
const { ccclass, property } = _decorator;

@ccclass('CarrulItems')
export class CarrulItems extends Component 
{
    @property(Node) public botones: Node[]=[];
    @property(Node) public items: Node[]=[];
    index:number=0;
    @property (Node) ma:Node=null;

    onLoad()
    {
        this.ma=find("Canvas/ManagerGeneral");
        for(let i=1;i<this.items.length;i++)
            {
                this.items[i].active=false;
            }
        
        //this.items[2].active=false;
        this.index=0;
        this.botones[1].active=false;
    }
    start() 
    {

    }

    update(deltaTime: number) 
    {
        if(this.index==0)
            {
                this.botones[1].active=false;
            }else
            {
                this.botones[1].active=true;
            }
     
            if(this.index==this.items.length-1)
            {
                this.botones[0].active=false;
            }
            else
            {
                this.botones[0].active=true;
            }
    }

    next()
    {
        console.log("next");
        if(this.index<this.items.length-1)
            {
                this.index++;
                this.items[this.index].active=true;
                this.items[this.index-1].active=false;
                console.log(this.index);
                //this.EstoyActivo(this.index);
                
            }
    }

    back()
    {
        console.log("back");
        if(this.index>0)
            {
                this.index--;
                this.items[this.index].active=true;
                this.items[this.index+1].active=false;
                console.log(this.index);
                //this.EstoyActivo(this.index);
            }
    }

    EstoyActivo(index_:number)
    {
        if(this.items[index_].children[0].active)
        {
            //this.ma.getComponent(SeleccionColor).MeSeleccionaron(index_);
        }
    }
}


