import { _decorator, Component, Node, Collider2D, find, Vec3, CCInteger, CCString, CCBoolean } from 'cc';
import { SpawnPiso } from './SpawnPiso';
import {Coche} from "./Coche";
const { ccclass, property } = _decorator;

@ccclass('RotarPiezas')
export class RotarPiezas extends Component {
    @property(Node)
    public Spawn : Node = null; //asignar desde inspector
    @property(Node)
    public ultimaPieza : Node = null;
    @property(CCBoolean)
    public Muerto : boolean = false;
    
    private ultima : boolean = false;
    @property(CCInteger)
    public rotateY : number = 0;
    @property(CCInteger)
    public nombre : number = 0;
    @property(CCInteger)
    private tiempo : number = 0;
    private index : number = 0;

    protected onLoad(): void {
        const node = find("coche");
        this.tiempo = node.getComponent(Coche).tiempoSpawn;
       this.node.on(Node.EventType.TOUCH_START, this.ontouch, this)
       this.node.on(Node.EventType.TOUCH_MOVE, this.ontouch, this)
       this.node.on(Node.EventType.TOUCH_END, this.ontouchcancel, this)
        this.node.on(Node.EventType.TOUCH_CANCEL, this.ontouchcancel,this)
        
    }
    ontouch(event: Touch){
       
        this.index +=1;
        if(this.ultimaPieza!=null){
           // console.log("rota " + this.ultimaPieza)
           this.rotateY = this.node.rotation.y + 90;
                this.rotateY = Math.floor(this.rotateY);
           /* switch(this.index){
                case 0: 
                
                break;
                case 1:  this.rotateY = this.node.rotation.y + 90;
                this.rotateY = Math.floor(this.rotateY);
                break;
                case 2:  this.rotateY = this.node.rotation.y + 90;
                this.rotateY = Math.floor(this.rotateY);
                break;
                case 3:  this.rotateY = this.node.rotation.y + 90;
                this.rotateY = Math.floor(this.rotateY);
                this.index= 0;
                break;
            }*/
           
           
            
            this.ultimaPieza.setRotationFromEuler(new Vec3(0, this.rotateY,0))
        }
    }
    ontouchcancel(event: Touch){

    }
   
    cambiaultima(){
        const node = find("coche");
        this.tiempo = node.getComponent(Coche).tiempoSpawn;
        const numb = this.Spawn.getComponent(SpawnPiso).GameObjetc.length;
        //console.log("el valor del arreglo es igual a "+ numb.toString());
        this.ultimaPieza = this.Spawn.getComponent(SpawnPiso).GameObjetc[numb-1];
        this.rotateY = this.ultimaPieza.rotation.y;
        this.ultima = false;
       // console.log("Cambie algo");
    }
    start() {
        
    }

    update(deltaTime: number) {
        if (!this.ultima && !this.Muerto) {
            this.ultima = true;
            this.scheduleOnce(function () {
                this.cambiaultima();
 
            }, this.tiempo);
        }
    }
}


