import { _decorator, Component, Node, RigidBody, Vec3, CCFloat, CCBoolean, Collider, ICollisionEvent, find, CCInteger, ParticleSystem, Animation, Label } from 'cc';
import { info } from './info';
import { SpawnPiso } from './SpawnPiso';
import { RotarPiezas } from './RotarPiezas';
import { SelladoPieza } from './SelladoPieza';
import { FollowPlayer } from './FollowPlayer';
import { RemovePieza } from './RemovePieza';
import { ManagerGeneral } from './ManagerGeneral';
import { ButtonMessage } from './ButtonMessage';
const { ccclass, property } = _decorator;

@ccclass('Coche')
export class Coche extends Component {
    @property(CCFloat)
    public speed:number;
    private speedMax : number =20;
    private valormin : number = .1;
    @property(CCFloat)
    public tiempoSpawn : number = 0;
    @property(CCFloat)
    public tiempoRotacion : number = 0;
    @property(Vec3)
    public moveDirection : Vec3 = new Vec3;
    @property (CCFloat)
    public angle : number = 0;
    @property(Node)
    public nodeImage : Node = null;
    @property(RigidBody)
    private rigid:RigidBody = null;
    @property(CCBoolean)
    private inicio :boolean = false;
    private valorx : number = 0;
    private valory : number = 0;
    private negativ :boolean = false;
    @property(Node)
    private SpawnPisoNode : Node = null;
    @property(Node)
    private RotarPieza : Node = null;
    private aumenta : boolean = false;
    private GameOver : Node = null;
    @property(CCInteger)
    private indexP : number = 0;
    @property(Label)
    public ScoreText : Label = null;
    public Score : number = 0;
    private ScoreGameOver : Label = null;
    
    @property(ManagerGeneral)managerG:ManagerGeneral=null;
    @property(CCInteger) public contadorTarjetas:number=0;
    a:Node=null;


    protected onLoad(): void {
        this.a=find("Canvas/ManagerGeneral");
        
        this.Score = 0;
        this.rigid = this.node.getComponentInChildren(RigidBody);
        this.SpawnPisoNode = find("ManagerMapa")
        this.valorx = (this.speed/100) * -1;
        this.GameOver = find("Canvas/gameover");
        this.ScoreText = find("Canvas/Marcos/Score").getComponent(Label);
        this.ScoreGameOver = find("Canvas/gameover/fin/Score").getComponent(Label);
       
        this.ScoreText.string = this.Score.toString();
        this.GameOver.active=false;
       
        
       
        const collider = this.getComponentInChildren(Collider);
        if (collider) {
            // Registrar los eventos de colisión
            collider.on('onTriggerEnter', this.onCollisionEnter, this);
            //collider.on('onCollisionStay', this.onCollisionStay, this);
            collider.on('onCollisionExit', this.onCollisionExit, this);
        }
        this.SpawnPisoNode.getComponent(SpawnPiso).tiempo = this.tiempoSpawn;

        this.contadorTarjetas=this.managerG.contadorTarjetas;
        
    }
    start() {
        
        this.scheduleOnce(function(){
            this.inicio = true;
        },5)

       
        
    }
    AnularFuerzas(){
        if(!this.GameOver.active){
            this.GameOver.active = true;
            //  this.rigid.clearForces();
            this.valorx = 0;
            this.valory = 0;
            this.inicio=false;
            this.SpawnPisoNode.getComponent(SpawnPiso).Muerto = true;
            this.ScoreGameOver.string = this.ScoreText.string;
            // this.RotarPieza.getComponent(RotarPiezas).Muerto = true;
            this.managerG.getComponent(ButtonMessage).sendMessageToParent;
        }
        

    }

    onCollisionEnter(event: ICollisionEvent) {
        // Lógica cuando comienza una colisión
       //console.log('Colisión detectada al entrar con: ', event.otherCollider.name);
       if(event.otherCollider.getGroup()==64){
        const Padre = event.otherCollider.node.parent 
        this.RotarPieza.getComponent(FollowPlayer).derecha = Padre.getComponent(SelladoPieza).Derecha;
        //console.log (Padre.name);
        Padre.getComponent(SelladoPieza).Sellado = true;
        if(!Padre.getComponent(SelladoPieza).yaPaso){
            Padre.getChildByName("Particulas").getComponent(ParticleSystem).play();
            Padre.getComponent(SelladoPieza).yaPaso=true;
            this.Score +=1;
            this.ScoreText.string = this.Score.toString();
            ////////
        }
        Padre.getComponent(RemovePieza).remove();
       
        this.indexP = Padre.getComponent(SelladoPieza).IndexPieza;
        let name : number = event.otherCollider.node.getComponent(info).nombre;
       
        switch(name){
            case 0:
                //vertical;
               //this.rigid.clearVelocity();
               //this.rigid.clearForces();
               //this.rigid.applyLocalForce(new Vec3(this.moveDirection.x * -2.5,0,this.moveDirection.y * -2));
               this.node.setRotationFromEuler(new Vec3(0,0,0))
               this.valorx = (this.speed/100) * -1;
                this.valory = 0;
                break;
            case 1:
                //Izquierda
                this.valorx = 0;
               this.node.setRotationFromEuler(new Vec3(0,-90,0))
                this.valory = (this.speed/100) * -1;
               // this.valory = -1/100;
                break;
            case 2:
                //Derecha
               this.valorx = 0;
                this.node.setRotationFromEuler(new Vec3(0,90,0))
                this.valory = (this.speed/100) * 1;
                //this.valory = 1/100;
                
                break;
            case 3:
               this.node.setRotationFromEuler(new Vec3(0,0,0))
                this.valorx = (this.speed/100) * -1;
                this.valory = 0;
                
               
                break;
            case 4:
                 //Horizontal
                 this.valorx = 0;

                
        }
        
       }
       if (event.otherCollider.getGroup() == 16){
            //console.log("Toque tarejta " + event.otherCollider.getGroup().toString())
            event.otherCollider.node.getComponent(Collider).destroy();
            this.SpawnPisoNode.getComponent(SpawnPiso).Power(this.indexP);
            event.otherCollider.node.getComponent(Animation).play("Tarjeta-Desaparece");
            console.log("ORDENA");

            /*CONTADOR TARJETAS*/
            this.contadorTarjetas+=5;
            console.log("this.contadorTarjetas"+this.contadorTarjetas);
            this.managerG.contadorTarjetas=this.contadorTarjetas;
            this.managerG.contadortarjetas = this.contadorTarjetas.toString();
            console.log("this.managerG.contadorTarjetas"+this.managerG.contadorTarjetas);
            this.managerG.Save();
       }
    }
    onCollisionExit(event:ICollisionEvent){
       // console.log('Colisión detectada al salir con: ', event.otherCollider.node.name);
    }
    avanza(){
        this.moveDirection.x += this.valorx;
        this.moveDirection.y += this.valory;
        this.node.setPosition(new Vec3 ( this.moveDirection.x, 0,this.moveDirection.y));
        //this.moveDirection.y += this.valory;

       // this.angle = Math.atan2(this.moveDirection.x, this.moveDirection.y) * (-180 / Math.PI);
        //Vec3.normalize(this.moveDirection, this.moveDirection);
       // this.nodeImage.angle=this.angle;
        //this.rigid.applyLocalForce(new Vec3(this.moveDirection.x * this.speed,0,this.moveDirection.y * this.speed));
    }
    aumentaDif(){
        this.speed += .5;
        if(this.speed >= this.speedMax){
            this.speed = this.speedMax;
        }
        this.tiempoSpawn -= .8
        
       
        if(this.tiempoSpawn <= this.valormin){
            this.tiempoSpawn = this.valormin;
        }
        this.SpawnPisoNode.getComponent(SpawnPiso).tiempo = this.tiempoSpawn;
        this.tiempoRotacion -= .08;
        this.aumenta=false;
        
    }
    update(deltaTime: number) {
        
        if(this.inicio){
            this.avanza();
            if(!this.aumenta)
            {
                this.aumenta = true;
                this.scheduleOnce(function(){
                    this.aumentaDif()
                },10)
            }
        }
    }

    Actualizar()
    {
        this.managerG.contadorTarjetas=this.contadorTarjetas;
        console.log(this.managerG.contadorTarjetas);

        this.managerG.Save();
    }
}


