import { _decorator, Camera, Component, Node, math, PhysicsSystem, geometry,Quat, Vec2, Vec3, EventTouch, input, Input, quat, Collider } from 'cc';
import { RotarPiezas } from './RotarPiezas';
import { SelladoPieza } from './SelladoPieza';
const { ccclass, property } = _decorator;

@ccclass('Raycast')
export class Raycast extends Component {

    @property(Camera)
    mainCamera : Camera = null;
    @property(Vec3)
    rotationAngle: Vec3 = new Vec3(0, 45, 0)
    private rotateY : number;
    private index : number = -1;
    protected onLoad(): void {
        input.on(Input.EventType.TOUCH_START, this.onTouchStart, this);
    }

    start() {
        

    }
    onTouchStart(event:EventTouch){
        PhysicsSystem.instance.resetAccumulator();

        const touch = event.getLocation();
        const ray = new geometry.Ray();
         // Generar el rayo desde la cámara y la posición del toque
         this.mainCamera?.screenPointToRay(touch.x, touch.y, ray);

         // Usar el sistema de física para verificar colisiones
         if (PhysicsSystem.instance.raycast(ray)) {
             const results = PhysicsSystem.instance.raycastResults;
             for (let i = 0; i < results.length; i++) {
                 const hit = results[i];
                 const clickedNode: Node= hit.collider.node;
                 //console.log(clickedNode.getComponent(Collider).getGroup().toString() + "grupo de lo que tocaste")
                 if(clickedNode.getComponent(Collider).getGroup()==1 && !clickedNode.getComponent(SelladoPieza).Sellado){
                    //console.log("Es la pieza" + clickedNode.name);
                    this.rotateNode(clickedNode)
                 }
                
                  /*  if(clickedNode.parent.getComponent(SelladoPieza).Sellado){
                        
                    }
                    else{
                        console.log("rota pieza")
                        //clickedNode.parent;
     
                     // Aquí puedes agregar lo que quieres hacer cuando el objeto es clickeado
                     console.log("Objeto clickeado:", clickedNode.name);
                     
                     //this.rotapieza(clickedNode);
                     this.rotateNode(clickedNode.parent)
                    }*/
                    
                 
                 
             }
         }

    }
    rotapieza(RotaPieza:Node){
        const node = RotaPieza.parent;
        this.rotateY += 90;
        this.rotateY = Math.floor(this.rotateY);
        if(this.rotateY >= 360){
            this.rotateY = 0;
        }    
       
        node.setRotationFromEuler(new Vec3(0, this.rotateY,0));
    }
    rotateNode(node: Node) {
        // Convierte el ángulo a una rotación en cuaternión y aplícala
       // if(node.name == "ManagerMAp")
        const currentRotation = node.getRotation();
        const additionalRotation = new Quat();
        Quat.fromEuler(additionalRotation, this.rotationAngle.x, this.rotationAngle.y, this.rotationAngle.z);
        const newRotation = new Quat();
         math.Quat.multiply (newRotation, currentRotation, additionalRotation)

        node.setRotation(newRotation);
    }
    onDestroy() {
        input.off(Input.EventType.TOUCH_START, this.onTouchStart, this);
    }

    update(deltaTime: number) {
        
    }
}


